	module.exports = "<h2 id=overview>Overview</h2> <p>The DataList component provides a flexible alternative to the Table component, wherein individual data points may or may not exist within each row. DataList relies upon <a href=https://pf-next.com/layouts/Toolbar/examples/ >PatternFly layouts</a> to achieve desired presentation within <code>pf-c-data-list__cell</code>s. DataLists do not have headers. If headers are required, use the <a href=https://pf-next.com/components/Table/examples/ >Table component</a>.</p> ";

